import React from 'react'
import{HStack,Avatar,Text} from '@chakra-ui/react'


const Message = ({text,uri,user="other"}) => {
  return (
  <HStack  alignSelf={user==="me"?"flex-end":"flex-start"} bg={user==="me"?"rgb(37,211,102)":"rgba(79, 115, 79, 0.82)"}  color={"white"} marginY={".5rem"} paddingX={user==="me"? "4":"2"} paddingY={1} border={"1px solid transparent"} borderRadius={".7rem"} >

     {
       user === "other" && <Avatar src={uri} />
     }


     <Text>{text}</Text>
     {
       user === "me" && <Avatar src={uri} />
     }
      


    </HStack>

  )
}

export default Message
